/**
 *    hook
 *
 *  :
 * - ESC:  
 * - Ctrl/Cmd + N:   
 */

import { useEffect } from "react";

interface UseKanbanKeyboardProps {
  showTaskAddModal: boolean;
  showTaskDetailModal: boolean;
  setShowTaskAddModal: (show: boolean) => void;
  setShowTaskDetailModal: (show: boolean) => void;
  setSelectedTask: (task: null) => void;
}

export function useKanbanKeyboard({
  showTaskAddModal,
  showTaskDetailModal,
  setShowTaskAddModal,
  setShowTaskDetailModal,
  setSelectedTask,
}: UseKanbanKeyboardProps) {
  useEffect(() => {
    const handleKeyPress = (e: KeyboardEvent) => {
      // ESC:  
      if (e.key === "Escape") {
        if (showTaskAddModal) {
          setShowTaskAddModal(false);
        }
        if (showTaskDetailModal) {
          setShowTaskDetailModal(false);
          setSelectedTask(null);
        }
        return;
      }

      //   input/textarea   
      if (
        showTaskAddModal ||
        showTaskDetailModal ||
        document.activeElement?.tagName === "INPUT" ||
        document.activeElement?.tagName === "TEXTAREA"
      ) {
        return;
      }

      // Ctrl/Cmd + N:   
      if (
        e.code === "KeyN" &&
        (e.ctrlKey || e.metaKey) &&
        !e.shiftKey &&
        !e.altKey
      ) {
        e.preventDefault();
        setShowTaskAddModal(true);
        return;
      }
    };

    window.addEventListener("keydown", handleKeyPress);
    return () => window.removeEventListener("keydown", handleKeyPress);
  }, [
    showTaskAddModal,
    showTaskDetailModal,
    setShowTaskAddModal,
    setShowTaskDetailModal,
    setSelectedTask,
  ]);
}
